<template>
    <el-tabs type="border-card">
        <el-tab-pane label="学海" v-loading="loading" >
            <el-upload
                class="upload-demo"
                drag
                :on-success="successUpload"
                :before-upload="progressUpload"
                :action="uploadUrl"
                multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload>
        </el-tab-pane>
        <el-tab-pane label="学海一" v-loading="loading1" >
            <el-upload
                class="upload-demo"
                drag
                :on-success="successUpload1"
                :before-upload="progressUpload1"
                :action="uploadUrl"
                multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload>
        </el-tab-pane>
        <el-tab-pane label="学海二" v-loading="loading2" >
            <el-upload
                class="upload-demo"
                drag
                :on-success="successUpload2"
                :before-upload="progressUpload2"
                :action="uploadUrl"
                multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload> 
        </el-tab-pane>
    </el-tabs>
</template>

<script>
import { Transform } from "@/api/transform";
export default {
  data() {
    return {
      loading: false,
      loading1: false,
      loading2: false,
      uploadUrl: Transform.uploadUrl
    };
  },
  methods: {
    /**
     * @description 成功后的回调参数
     * @function successUpload
     * @param  {type} response 成功后的毁掉参数
     */
    successUpload(response) {
      if (response.code === 200) {
        this.getuuid(0, response.url);
      }
    },
    /**
     * @description 上传进行中
     * @function progressUpload
     * @return {type} {description}
     */
    progressUpload() {
      this.loading = true;
    },
    /**
     * @description 成功后的回调参数
     * @function successUpload
     * @param  {type} response 成功后的毁掉参数
     */
    successUpload1(response) {
      if (response.code === 200) {
        this.getuuid(1, response.url);
      }
    },
    /**
     * @description 上传进行中
     * @function progressUpload
     * @return {type} {description}
     */
    progressUpload1() {
      this.loading1 = true;
    },
    /**
     * @description 成功后的回调参数
     * @function successUpload
     * @param  {type} response 成功后的毁掉参数
     */
    successUpload2(response) {
      if (response.code === 200) {
        this.getuuid(2, response.url);
      }
    },
    /**
     * @description 上传进行中
     * @function progressUpload
     * @return {type} {description}
     */
    progressUpload2() {
      this.loading2 = true;
    },
    /**
     * @function {function name}
     * @param  {type} type {description}
     * @return {type} {description}
     */
    getuuid(type, url) {
      let data = {
        url: "http://ts.whytouch.com/mass_up.php",
        file: url,
        token: "a64fcbafdf6bada44fe1d441a46feae1",
        mode: 0,
        aut: "xuehai-kt4.0",
        fn: url
      };

      if (type === 0) {
        setTimeout(() => {
          Transform.getuuid(data)
            .then(res => {
              let id = res.data.replace(/\ufeff/g, "");
              window.open(Transform.getzip + id);
              this.loading = false;
            })
            .catch(err => {});
        }, 200);
      } else if (type == 1) {
        Transform.getuuid1(data)
          .then(res => {
            let id = res.data.replace(/\ufeff/g, "");
            window.open(Transform.getzip1 + id);
            this.loading1 = false;
          })
          .catch(err => {});
      } else {
        Transform.getuuid2(data)
          .then(res => {
            let id = res.data.replace(/\ufeff/g, "");
            window.open(Transform.getzip2 + id);
            this.loading2 = false;
          })
          .catch(err => {});
      }
    }
  },
  mounted() {}
};
</script>

